<template>
    <div>
        <loading :active.sync="isLoading">
          <img src="./../../assets/images/Magnify-1s-200px.svg" alt="">
        </loading>
        <div class="text-right mt-4">
            <button class="btn btn-outline-ok" @click.prevent="openModal(true)">建立新產品</button>
        </div>
        <div class="table-responsive">
            <table class="table mt-4">
                <thead>
                    <tr>
                        <th class="d-none d-md-block">分類</th>
                        <th>產品名稱</th>
                        <th>原價</th>
                        <th>售價</th>
                        <th class="text-center">是否啟用</th>
                        <th width="130" class="text-center d-none d-md-table-cell">編輯</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="item in products" :key="item.id">
                        <td class="d-none d-md-table-cell">{{item.category}}</td>
                        <td>{{item.title}}</td>
                        <td class="text-right font-weight-bold">{{item.origin_price | currency}}</td>
                        <td class="text-right font-weight-bold">{{item.price | currency}}</td>
                        <td class="text-center">
                            <span v-if="item.is_enabled" class="text-success">啟用</span>
                            <span v-else class="text-danger">未啟用</span>
                        </td>
                        <td class="text-center d-none d-md-table-cell">
                            <button class="btn btn-outline-ok btn-sm mr-1" @click.prevent="openModal(false, item)">修改</button>
                            <button class="btn btn-outline-no btn-sm" @click.prevent="openDeleteModal(item)">刪除</button>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
        <page :pagination="pagination" @getPages="getProducts"></page>
        <div class="modal fade" id="productModal" tabindex="-1" role="dialog"
            aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog modal-lg" role="document" style="margin-top: 100px">
                <div class="modal-content border-0">
                <div class="modal-header bg-dark text-white">
                    <h5 class="modal-title" id="exampleModalLabel">
                    <span>新增產品</span>
                    </h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <div class="row">
                    <div class="col-sm-4">
                        <div class="form-group">
                        <label for="image">輸入圖片網址</label>
                        <input type="text" class="form-control" id="image"
                            placeholder="請輸入圖片連結" v-model="tempProduct.imageUrl">
                        </div>
                        <div class="form-group">
                        <label for="customFile">或 上傳圖片
                            <i class="fas fa-cog fa-spin" v-if="status.fileupLoading"></i>
                        </label>
                        <input type="file" id="customFile" class="form-control"
                            ref="files" @change="uploadFile">
                        </div>
                        <img img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                        class="img-fluid" alt="" :src="tempProduct.imageUrl">
                    </div>
                    <div class="col-sm-8">
                        <div class="form-group">
                        <label for="title">標題</label>
                        <input type="text" class="form-control" id="title"
                            placeholder="請輸入標題" v-model="tempProduct.title">
                        </div>

                        <div class="form-row">
                        <div class="form-group col-md-6">
                            <label for="category">分類</label>
                            <input type="text" class="form-control" id="category"
                            placeholder="請輸入分類"  v-model="tempProduct.category">
                        </div>
                        <div class="form-group col-md-6">
                            <label for="price">單位</label>
                            <input type="unit" class="form-control" id="unit"
                            placeholder="請輸入單位" v-model="tempProduct.unit">
                        </div>
                        </div>

                        <div class="form-row">
                        <div class="form-group col-md-6">
                        <label for="origin_price">原價</label>
                            <input type="number" class="form-control" id="origin_price"
                            placeholder="請輸入原價" v-model="tempProduct.origin_price">
                        </div>
                        <div class="form-group col-md-6">
                            <label for="price">售價</label>
                            <input type="number" class="form-control" id="price"
                            placeholder="請輸入售價" v-model="tempProduct.price">
                        </div>
                        </div>
                        <hr>

                        <div class="form-group">
                        <label for="description">產品描述</label>
                        <textarea type="text" class="form-control" id="description"
                            placeholder="請輸入產品描述" v-model="tempProduct.description"></textarea>
                        </div>
                        <div class="form-group">
                        <label for="content">說明內容</label>
                        <textarea type="text" class="form-control" id="content"
                            placeholder="請輸入產品說明內容" v-model="tempProduct.contnet"></textarea>
                        </div>
                        <div class="form-group">
                        <div class="form-check">
                            <input class="form-check-input" type="checkbox"
                            id="is_enabled"  v-model="tempProduct.is_enabled"
                            :true-value = '1'
                            :false-value= '0'>
                            <label class="form-check-label" for="is_enabled">
                            是否啟用
                            </label>
                        </div>
                        </div>
                    </div>
                    </div>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-outline-deeper" data-dismiss="modal">取消</button>
                    <button type="button" class="btn btn-ok text-white" @click="updateProduct">確認</button>
                </div>
                </div>
            </div>
            </div>
            <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog"
            aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog" role="document"  style="margin-top: 100px">
                <div class="modal-content border-0">
                <div class="modal-header bg-danger text-white">
                    <h5 class="modal-title" id="exampleModalLabel">
                    <span>刪除產品</span>
                    </h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    是否刪除 <strong class="text-danger">{{ tempProduct.title }}</strong> 商品(刪除後將無法恢復)。
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
                    <button type="button" class="btn btn-danger" @click="deleteProduct"
                    >確認刪除</button>
                </div>
                </div>
            </div>
        </div>
      <Footer></Footer>
    </div>
</template>

<script>
import $ from 'jquery';

export default {
  data() {
    return {
      products: [],
      tempProduct: {},
      pagination: {},
      isNew: false,
      status: {
        fileupLoading: false,
      },
    };
  },
  methods: {
    getProducts(page = 1) {
      const vm = this;
      const api = `${process.env.VUE_APP_PATH}/api/${process.env.VUE_APP_CUSTOMER}/admin/products?page=${page}`;
      vm.$store.dispatch('updateLoading', true);
      this.$http.get(api).then((response) => {
        vm.$store.dispatch('updateLoading', false);
        vm.products = response.data.products;
        vm.pagination = response.data.pagination;
      });
    },
    openModal(isNew, item) {
      if (isNew) {
        this.tempProduct = {};
        this.isNew = true;
      } else {
        this.tempProduct = Object.assign({}, item);
        this.isNew = false;
      }
      $('#productModal').modal('show');
    },
    updateProduct() {
      const vm = this;
      let api = `${process.env.VUE_APP_PATH}/api/${process.env.VUE_APP_CUSTOMER}/admin/product`;
      let other = 'post';
      if (!vm.isNew) {
        other = 'put';
        api = `${process.env.VUE_APP_PATH}/api/${process.env.VUE_APP_CUSTOMER}/admin/product/${vm.tempProduct.id}`;
      }
      this.$http[other](api, { data: vm.tempProduct }).then((response) => {
        if (response.data.success) {
          vm.getProducts();
          $('#productModal').modal('hide');
        } else {
          vm.getProducts();
        }
      });
    },
    openDeleteModal(item) {
      const vm = this;
      vm.tempProduct = item;
      $('#delProductModal').modal('show');
    },
    deleteProduct() {
      const vm = this;
      const api = `${process.env.VUE_APP_PATH}/api/${process.env.VUE_APP_CUSTOMER}/admin/product/${vm.tempProduct.id}`;
      this.$http.delete(api).then(() => {
        $('#delProductModal').modal('hide');
        vm.getProducts();
      });
    },
    uploadFile() {
      const vm = this;
      const api = `${process.env.VUE_APP_PATH}/api/${process.env.VUE_APP_CUSTOMER}/admin/upload`;
      const formData = new FormData();
      const upload = vm.$refs.files.files[0];
      formData.append('file-to-upload', upload);
      vm.status.fileupLoading = true;
      this.$http.post(api, formData, { headers: { 'Content-Type': 'multipart/form-data' } }).then((response) => {
        if (response.data.success) {
          vm.status.fileupLoading = false;
          vm.$set(vm.tempProduct, 'imageUrl', response.data.imageUrl);
        } else {
          this.$bus.$emit('message:push', response.data.message, 'no');
        }
      });
    },
  },
  created() {
    this.getProducts();
  },
  computed: {
    isLoading() {
      return this.$store.state.isLoading;
    },
  },
};
</script>
